/* Estilos para el header */
.header {
  height:70px;
  background-color: #007bff;
  color: white;
  display: flex;
  align-items: center;
  justify-content: flex-end; /* Alinea los elementos a la izquierda y derecha */
  padding: 10px 20px;
}


.header-toggle {
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 24px;
  margin-right: 10px; /* Agrega margen derecho para moverlo a la derecha */
}

/* Estilos para el menú desplegable */
.header-menu {
  position: absolute;
  top: 50px;
  right: 0;
  background-color: #333;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s;
}
    
  .header-abierto {
    visibility: visible; /* Muestra el menú cuando está abierto */
    opacity: 1;
  }
  
  /* Estilos para el nombre de usuario */
  .header-nombre-usuario {
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  header-ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  header-li {
    cursor: pointer;
    padding: 5px 0;
  }
  
  /* Estilos para la opción de Cerrar Sesión al pasar el mouse */
  header-li:hover {
    background-color: #0056b3; /* Color de fondo más oscuro al pasar el mouse */
  }
  